@include font-face("Inter-Medium-Italic", "Inter-SemiBoldItalic", "600", "italic");
@include font-face("Inter-Medium-Normal", "Inter-SemiBold", "600", "normal");
@include font-face("Inter-Regular", "Inter-Regular", "400", "normal");

.visually-hidden:not(:focus):not(:active),
input[type='checkbox'].visually-hidden,
input[type='radio'].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  font-size: 10px;
  scroll-behavior: smooth;
}

body {
  background: $color-background-body;
}

section {
  margin-bottom: 100px;
}

h1 {
  @include font("Inter-Medium-Normal", 7.2rem, 600, 1.05);
  color: $color-text-light;
}

h2 {
  @include font("Inter-Medium-Normal", 6rem, 600, 1.25);
  color: $color-text-dark;
}

h3 {
  @include font("Inter-Medium-Normal", 2.4rem, 600, 1.25);
  color: $color-text-dark;
}

span {
  @include font("Inter-Medium-Normal", 1rem, 600, 1.4);
  color: $color-text-dark;
}

p {
  @include font("Inter-Regular", 1.6rem, 400, 1.5);
  color: $color-text-dark;
}

em {
  font-family: "Inter-Medium-Italic";
  color: $color-text-accent;
}

strong {
  @include font("Inter-Medium-Normal", 2.4rem, 600, 1.25);
  color: $color-text-dark;
}
